<template>
  <div class="animated fadeIn">
    <user-head pageTitle="我的收藏"></user-head>
    <mt-cell 
    class="news-list"
    v-for="(item, index) in articleArr"
    :data-id="item.id"
    :title="item.title" 
    :label="item.time | dateFormat" 
    :key="index"
    :to="{name: 'newsDetail', params: {name: item.id}}"
    is-link>
      <img class="news-cover" slot="icon" :src="item.imgUrl">
      <div class="news-info">
        <span class="watch" title="点击数"><i class="icon watch-icon"></i>{{item.pageviews}}</span>
        <span class="comment" title="评论数"><i class="icon comment-icon"></i>0</span>
      </div>
    </mt-cell>
  </div>
</template>

<script>

import UserHead from './UserHead.vue'

export default {
  data () {
    return {
      userInfo: [],
      articleArr: []
    }
  },

  methods: {
    getArticleInfo(){
      this.sendReq('I0032', {
        UserId: this.userInfo[0],
        TypeId: 2,
        PageIndex: 1,
        PageSize: 10
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          res.data.data.forEach((item, index) => {
            item.time = new Date(parseInt(item.time.match(/\d+/)[0]));
            this.articleArr.push({
              id: item.Id,
              title: item.title,
              time: item.time,
              imgUrl: this.Api.imgUrl + item.pics,
              pageviews: item.praiseCount
            })
          })
          console.log(this.articleArr);
        }
      })
      .catch((err) => { console.log(err); });
    }
  },

  created(){
    this.userInfo = this.getCookie();
    this.getArticleInfo();
  },

  components: { UserHead }
}
</script>

<style lang="scss">
  @import "../style/mixin";
  .news-list {
    padding: 10px 0;
    border-bottom: 1px solid #e9e9e9;
    .mint-cell-wrapper {
      position: relative;
      .mint-cell-title {
        position: relative;
        line-height: 1.5;
        .mint-cell-text {
          display: block;
          padding-left: 120px;
          height: 48px;
          @include multiple-text-break;
        }
      }
    }
    .mint-cell-label {
      padding-left: 120px;
    }
    .mint-cell-value {
      position: absolute;
      bottom: 0;
      right: 10px;
      &.is-link {
        margin-right: 0;
      }
    }
    .news-cover {
      position: absolute;
      left: 0;
      top: 0;
      width: 110px;
      height: 76px;
    }
    .mint-cell-allow-right {
      display: none;
    }
  }
  .news-info {
    display: flex;
    color: #888;
    font-size: 12px;
    line-height: 1.5;
    span {
      position: relative;
      padding-left: 22px;
      flex: 1;
      &.watch {
        margin-right: 5px;
      }
      .icon {
        position: absolute;
        left: 4px;
        top: 3px;
        width: 14px;
        height: 14px;
        background-size: 100%;
        background-position: center;
      }
      .comment-icon {
        background-image: url(../assets/tab-comment.svg);
      }
      .watch-icon {
        background-image: url(../assets/fire.svg);
      }
    }
  }
</style>


