<template>
  <div class="newslive-container animated fadeIn">
    <div class="cur-date">
      <p class="date">{{curDate}}</p>
      <p class="month">{{curMonth}}月</p>
    </div>
    <div 
    class="news-list-wrapper"
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-distance="1">
      <div class="newslive-item" v-for="item in newsLive">
        <div class="newslive-icon">
          <div class="dot"></div>
        </div>
        <div class="time">{{item.time | timeFormat}}</div>
        <div class="newslive-content" v-html="item.title">
        </div>
      </div>
      <mt-cell class="loading" v-show="loading">
        <mt-spinner type="triple-bounce" color="#df5000">加载中...</mt-spinner>
      </mt-cell>
    </div>
  </div>
</template>

<script>

export default {
  props: ['selectState'],
  data () {
    return {
      newsLive: [],
      curDay: new Date(),
      loading: false,
      pageIndex: 0,
      pageSize: 10
    }
  },

  computed: {
    curMonth(){
      let month = this.curDay.getMonth() + 1;
      return month < 10 ? '0' + month : month;
    },
    curDate(){
      let date = this.curDay.getDate();
      return date < 10 ? '0' + date : date;
    }
  },

  methods: {
    getNewsLive(reflash){

      if(reflash){
        this.newsLive.length = 0;
        this.pageIndex = 1;
      }

      this.sendReq('I0005', {
        isRadio: -1,
        orderBy: "Time desc",
        pageIndex: this.pageIndex,
        pageSize: this.pageSize,
        Importance: this.selectState ? encodeURI('高') : ''
      })
      .then((res) => {
        if(res.data.StatusCode == 0){
          var arr = res.data.data;
          console.log(arr);
          this.loading = false;
          if(arr.length < this.pageSize){
            this.loading = true;
          }
          var reg = /\d+/;
          arr.forEach((item, index) => {
            item.Time = parseInt(item.Time.match(reg)[0]);
            item.Time = new Date(item.Time);
            this.newsLive.push({
              time: item.Time,
              title: item.Title
            })
          })
          if(this.pageIndex == 1){
            this.curDay = this.newsLive[0].time;
            window.addEventListener('scroll', this.checkDate);
          }
        }
      })
      .catch((err) => { console.log(err); });
    },
    loadMore(){
      this.loading = true;
      this.pageIndex += 1;
      this.getNewsLive();
    },
    checkDate(){
      let baseHeight = document.querySelector('.cur-date').offsetTop;
      this.newsLive.forEach((item, index) => {
        if(item.time.getDate() != this.curDay.getDate()){
          let domArr = document.querySelectorAll('.newslive-item');
          if(domArr[index].getBoundingClientRect().top - baseHeight < 10){
            this.curDay = item.time;
            return ;
          }
        }
      });
    }
  },

  beforeDestroy(){
    window.removeEventListener('scroll', this.checkDate);
  },

  watch: {
    selectState(){
      this.getNewsLive(true);
    }
  },

  created(){
    // console.log(this.selectState);
  }
}
</script>

<style lang="scss">
  .newslive-container {
    position: relative;
    padding-left: 22px;
    padding-right: 10px;
  }
  .cur-date {
    position: fixed;
    top: 65px;
    left: 3px;
    z-index: 100;
    border: 3px solid #0a79e5;
    padding: 3px;
    color: #0a79e5;
    background-color: #fff;
    text-align: center;
    line-height: 20px;
    .date {
      font-size: 20px;
    }
    .month {
      font-size: 14px;
    }
  }
  .newslive-item {
    position: relative;
    padding-left: 30px;
    border-left: 1px solid #f0f5f9;
    .newslive-icon {
      z-index: 1;
      position: absolute;
      top: 17px;
      left: -8px;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background-color: #f0f5f9;
      .dot {
        width: 5px;
        height: 5px;
        border: 1px solid #fff;
        border-radius: 50%;
        background-color: #0a79e5;
        margin: 5px auto;
      }
    }
    .time {
      display: inline-block;
      margin: 10px 0;
      padding: 0 15px;
      border-radius: 15px;
      line-height: 30px;
      color: #0a79e5;
      background-color: #f0f5f9;
      &::before {
        content: "";
        position: absolute;
        top: 24px;
        left: 0;
        width: 30px;
        height: 0;
        border-top: 2px solid #f0f5f9;
      }
    }
    .newslive-content {
      text-align: justify;
    }
  }
</style>


