<template>
  <div id="detail" class="animated fadeIn">

    <mt-navbar class="zdcj-news-nav" v-model="selectedType">
      <mt-tab-item v-for="item in newsNav" :id="item.id" :key="item.key" v-on:click.native="linkTo">{{item.id}}</mt-tab-item>
    </mt-navbar>

    <div class="news-banner">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="(item, index) in newsArr" :key="index" v-if="index<5">
          <router-link :to="{name: 'newsDetail', params: {name: item.id}}">
            <div class="news-banner-title">{{item.title}}</div>
            <img :src="item.imgUrl" :alt="item.title">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>

    <div 
    class="news-list-wrapper"
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-immediate-check=false
    infinite-scroll-distance="1">
      <mt-cell 
      class="news-list"
      v-for="(item, index) in newsArr"
      v-if="index>5"
      :data-id="item.id"
      :title="item.title" 
      :label="item.time | dateFormat" 
      :key="index"
      :to="{name: 'newsDetail', params: {name: item.id}}"
      is-link>
        <img class="news-cover" slot="icon" :src="item.imgUrl">
        <div class="news-info">
          <span class="watch" title="点击数"><i class="icon watch-icon"></i>{{item.pageviews}}</span>
          <span class="comment" title="评论数"><i class="icon comment-icon"></i>0</span>
        </div>
      </mt-cell>
      <mt-cell class="loading" v-show="loading">
        <mt-spinner type="triple-bounce" color="#df5000">加载中...</mt-spinner>
      </mt-cell>
    </div>

  </div>
</template>

<script>

export default {
  props: ['selectState'],
  data () {
    return {
      selectedType: '',
      msg: '正点头条',
      newsArr: [],
      loading: false,
      pageIndex: 0,
      pageSize: 10,
      newsNav: [
        {id: '全部', key: ''},
        {id: '外汇', key: '外汇'},
        {id: '贵金属', key: '贵金属'},
        {id: '央行', key: '央行'},
        {id: '原油', key: '原油'},
        {id: '行情', key: '行情'},
        {id: '国际', key: '国际'}
      ]
    }
  },

  computed: {
    typeId(){
      return this.selectedType == '全部' ? '' : this.selectedType;
    }
  },

  methods: {
    getNewsList(refresh){
      if(refresh){
        this.newsArr.length = 0;
        this.pageIndex = 1;
        this.pageSize = 20;
      }
      this.sendReq('I0003', {
        OrderBy: "time desc",
        TypeId: this.typeId,
        PageIndex: this.pageIndex,
        PageSize: this.pageSize
      })
      .then((res) => {
        var newsList = res.data.data;
        var reg = /\d+/;
        
        this.loading = false;

        if(newsList.length < this.pageSize){
          this.loading = true;
        }

        newsList.forEach((item, index) => {
          item.time = parseInt(item.time.match(reg)[0]);
          item.time = new Date(item.time);
          this.newsArr.push({
            id: item.Id,
            title: item.title,
            imgUrl: this.Api.imgUrl + item.pics,
            pageviews: item.praiseCount,
            time: item.time
          });
        })
        
        console.log(this.newsArr);
      })
      .catch((err) => { console.log(err); });
    },
    loadMore(){
      this.loading = true;
      this.pageIndex += 1;
      this.pageSize = 10;
      this.getNewsList();
    },
    linkTo(){
      history.pushState({}, '', '#/headlines?type=' + this.selectedType);
      this.getNewsList(true);
    }
  },

  watch: {
    '$route' (to, from) {
      this.selectedType = to.query.type || '全部';
      this.getNewsList(true);
    }
  },

  created(){
    this.selectedType = this.$route.query.type || '全部';
    this.getNewsList(true);
  }
}
</script>

<style lang="scss">
  @import "./style/mixin";
  .news-list {
    padding: 10px 0;
  }

  .zdcj-news-nav {
    position: fixed;
    top: 50px;
    left: 0;
    z-index: 99;
    width: 100%;
    display: flex;
    background-color: #eef6fe;
    a {
      display: block;
      flex: 1;
      color: #333;
      line-height: 48px;
      text-align: center;
      font-size: 14px;
      &.active {
        color: #26a2ff;
      }
    }
    .mint-tab-item {
      color: #333;
      &.is-selected {
        border-bottom: none;
        margin-bottom: 0;
      }
    }
    .mint-tab-item-label {
      font-size: 14px;
    }
  }
  .news-container {
    margin-bottom: 60px;
  }
  .news-banner {
    height: 200px;
    margin-top: 98px;
    .mint-swipe-item {
      img {
        width: 100%;
        height: 100%;
      }
    }
    .mint-swipe-indicators {
      left: auto;
      transform: none;
      right: 2%;
    }
    .mint-swipe-indicator {
      background-color: #fff;
      opacity: 1;
      &.is-active {
        background-color: #ff3232;
      }
    }
    .news-banner-title {
      position: absolute;
      left: 0;
      bottom: 0;
      box-sizing: border-box;
      width: 100%;
      height: 40px;
      line-height: 40px;
      padding-left: 10px;
      padding-right: 80px;
      @include multiple-text-break(1);
      color: #fff;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
  .zdcj-tabbar {
    background: none;
    @include nav-style;
  }

  .news-list {
    border-bottom: 1px solid #e9e9e9;
    .mint-cell-wrapper {
      position: relative;
      .mint-cell-title {
        position: relative;
        line-height: 1.5;
        .mint-cell-text {
          display: block;
          padding-left: 120px;
          height: 48px;
          @include multiple-text-break;
        }
      }
    }
    .mint-cell-label {
      padding-left: 120px;
    }
    .mint-cell-value {
      position: absolute;
      bottom: 0;
      right: 10px;
      &.is-link {
        margin-right: 0;
      }
    }
    .news-cover {
      position: absolute;
      left: 0;
      top: 0;
      width: 110px;
      height: 76px;
    }
    .mint-cell-allow-right {
      display: none;
    }
  }

  .news-info {
    display: flex;
    color: #888;
    font-size: 12px;
    line-height: 1.5;
    span {
      position: relative;
      padding-left: 22px;
      flex: 1;
      &.watch {
        margin-right: 5px;
      }
      .icon {
        position: absolute;
        left: 4px;
        top: 3px;
        width: 14px;
        height: 14px;
        background-size: 100%;
        background-position: center;
      }
      .comment-icon {
        background-image: url(./assets/tab-comment.svg);
      }
      .watch-icon {
        background-image: url(./assets/fire.svg);
      }
    }
  }

  .loading {
    border-bottom: none;
    .mint-cell-wrapper {
      justify-content: center;
    }
    .mint-cell-title {
      display: none;
    }
  }
</style>


