import axios from 'axios'
import Api from './urlConfig.js'

export default{
  install(Vue,options)
  {
    Vue.prototype.setCookie = function (userInfo) {
      var cookieStr = 'mlogin0001=' + userInfo.join('|');
      this.$cookie.set('zdcjcookie', encodeURI(cookieStr), 1);
    };
    Vue.prototype.getCookie = function () {
      return decodeURI(this.$cookie.get('zdcjcookie')).replace(/mlogin0001=/, '').split('|');
    };
    Vue.prototype.checkLogin = function () {
      return this.$cookie.get('zdcjcookie');
    };
    Vue.prototype.sendReq = function (cmd, data) {
      if(!cmd || !data){
        alert('参数不全！');
        return ;
      }
      var args = {
          channel: "0001",
          key: "e5c6e5ccec361baebcf79859e49c5526",
          Auth: "",
          cmd: cmd,
          data: data
      };
      return axios.post(this.Api.apiBaseUrl, JSON.stringify(args));
    };
    Vue.prototype.uploadPic = function (userId, data) {
      return axios.post(this.Api.avatarUrl + '/Interface/Ajax/upload.ashx?UserId=' + userId, data);
    }
    Vue.prototype.Api = Api;
  }
}



