      </mt-tab-container-item>
    </mt-tab-container>
    <div class="sociality-container login">
      <p class="sociality-text">第三方账号登录</p>
      <mt-tabbar>
        <mt-tab-item id="发现">
          <div class="qq-icon icon" slot="icon">
            <img src="../assets/qq.svg">
          </div>
        </mt-tab-item>
        <mt-tab-item id="外卖">
          <div class="weibo-icon icon" slot="icon">
            <img src="../assets/weibo.svg">
          </div>
        </mt-tab-item>
        <mt-tab-item id="订单">
          <div class="wechat-icon icon" slot="icon">
            <img src="../assets/wechat.svg">
          </div>
        </mt-tab-item>
      </mt-tabbar>
    </div>
    <mt-popup
      class="popup-tips"
      v-model="popupVisible"
      position="top"
      :modal='false'>
      {{popUpText}}
    </mt-popup>
  </div>
</template>

<script>

export default {
  props: ['showSelf'],
  data () {
    return {
      active: 'tab-login',
      logEmail: '',
      logPWD: '',
      regEmail: '',
      regPWD: '',
      regCheckcode: '',
      popupVisible: false,
      popUpText: '',
      isSendEmail: false
    }
  },

  methods: {
    sendEmail(){
      this.sendReq('I0051', {
        EmailAddress: this.regEmail
      })
      .then((res) => {
        if(res.data.StatusCode == 0){
          this.isSendEmail = true;
          this.showPopup(res.data.Info);
        }
      })
      .catch((err) => { console.log(err); });
    },
    regAccount(send){
      this.$validator.validate('regEmail', this.regEmail).then((result) => {
        if(result){
          this.$validator.validate('regPWD', this.regPWD).then((info) => {
            if(info){
              if(send == true){
                this.sendEmail();
                return ;
              }
              this.$validator.validate('regCheckcode', this.regCheckcode).then((rst) => {
                if(rst){
                  this.sendReq('I0052', {
                    email: this.regEmail,
                    password: this.regPWD,
                    emailCheck: this.regCheckcode
                  })
                  .then((res) => {
                    if(res.data.StatusCode == 0){
                      this.resoveRes(res);
                    }
                  })
                  .catch((err) => { console.log(err); });
                } else {
                  this.showPopup(this.errors.first('regCheckcode'));
                }
              })
            } else {
              this.showPopup(this.errors.first('regPWD'));
            }
          })
        } else {
          this.showPopup(this.errors.first('regEmail'));
        }
      });
    },
    logAccount(){
      this.$validator.validate('logEmail', this.logEmail).then((result) => {
        if(result){
          this.$validator.validate('logPWD', this.logPWD).then((info) => {
            if(info){
              console.log('验证通过！');
              this.sendReq('I0015', {
                loginType: 1,
                email: this.logEmail,
                password: this.logPWD
              })
              .then((res) => {
                console.log(res);
                if(res.data.StatusCode == 0){
                  this.resoveRes(res);
                }
              })
              .catch((err) => { console.log(err); });
            } else {
              this.showPopup(this.errors.first('logPWD'));
            }
          })
        } else {
          this.showPopup(this.errors.first('logEmail'));
        }
      });
    },
    showPopup(text, callback){
      this.popupVisible = true;
      this.popUpText = text;
      setTimeout(() => {
        this.popupVisible = false;
        if(callback){
          callback();
        }
      }, 3000);
    },
    resoveRes(res){
      this.showPopup(res.data.Info, () => {
        this.$router.push({path:'/user'});
      });
      var resData = res.data.data;
      var userInfo = [resData.Id, resData.NickName, resData.UserPic , resData.TrueName, resData.Email, resData.Mobile, resData.QQOpenID, resData.WeiBoOpe, resData.Province, resData.City, resData.BirthDay, resData.Sex, resData.Industry, resData.UserRole];
      this.setCookie(userInfo);
    }
  },

  created(){
  }
}
</script>

<style lang="scss">
  @import '../style/mixin';
  .user-login-container {
    @include fixed-fill-box;
    padding: 20px;
    color: #fff;
    background-image: url(../assets/login-bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    .turn-back {
      display: block;
      width: 30px;
      height: 40px;
      margin-bottom: 40px;
    }
    .user-form-container {
      margin-bottom: 20%;
      .mint-tab-container-item {
        box-sizing: border-box;
        padding: 0 5%;
      }
    }
    .user-form-tab {
      line-height: 30px;
      height: 30px;
      margin-bottom: 30px;
      a {
        opacity: 0.6;
        font-size: 20px;
        &.reg-btn {
          margin-right: 10px;
        }
        &.active {
          opacity: 1;
          font-size: 28px;
        }
      }
    }
    .form-area {
      position: relative;
      margin-bottom: 25px;
      .checkcode-area {
        width: 41%;
      }
      .checkcode-btn {
        position: absolute;
        top: 0;
        right: 0;
        width: 38%;
        height: 100%;
        font-size: 16px;
      }
    }
    .input-area {
      position: relative;
      padding-left: 48px;
      background-color: #fff;
      border-radius: 4px;
      .mint-cell {
        background-color: transparent;
      }
      .tips {
        position: absolute;
        left: 0;
        top: 48px;
        color: red;
        font-size: 14px;
        line-height: 25px;
      }
      .icon {
        position: absolute;
        left: 12px;
        top: 12px;
        width: 24px;
        height: 24px;
      }
    }
    .submit-btn {
      height: 48px;
      margin-bottom: 10px;
    }
    .login-form-tips {
      text-align: center;
      opacity: 0.6;
    }
    .popup-tips {
      width: 100%;
      height: 50px;
      line-height: 50px;
      text-align: center;
      background-color: rgba(0,0,0,.7);
    }
  }
  .sociality-container {
    &.login {
      .sociality-text {
        &::before, &::after {
          width: 30%;
        }
      }
      .icon {
        background-color: #fff;
      }
    }
  }
</style>


