<template>
  <div id="footer-bar" class="animated fadeIn">
    <mt-tabbar class="zdcj-tabbar" v-model="selectedTab" fixed>
      <mt-tab-item id="时讯" href="#/newslive">
        <img slot="icon" src="../assets/nav-news.svg">
        时讯
      </mt-tab-item>
      <mt-tab-item id="行情" href="#/markets">
        <img slot="icon" src="../assets/nav-markets.svg">
        行情
      </mt-tab-item>
      <mt-tab-item id="日历" href="#/calendar">
        <img slot="icon" src="../assets/nav-calendar.svg">
        日历
      </mt-tab-item>
      <mt-tab-item id="我的" href="#/user">
        <img slot="icon" src="../assets/nav-user.svg">
        我的
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>

export default {
  data () {
    return {
      selectedTab: ''
    }
  },

  methods: {
    checkCurPage(route){
      switch(route.name)
      {
        case 'userPage':
          this.selectedTab = '我的';
          break;
        case 'kuaixun':
          this.selectedTab = '时讯';
          break;
        case 'zdtt':
          this.selectedTab = '时讯';
          break;
        case 'hangqing':
          this.selectedTab = '行情';
          break;
        case 'rili':
          this.selectedTab = '日历';
          break;
      }
    }
  },

  watch: {
    '$route' (to, from) {
      this.checkCurPage(to);
    }
  },

  created(){
    this.checkCurPage(this.$route);
  }
}
</script>

<style lang="scss">
</style>


