<template>
  <div id="news-wrap" class="animated fadeIn">
    <header class="zdcj-header">
      <mt-navbar class="zdcj-h-nav" v-model="selectedNav">
        <mt-tab-item id="newslive" href="#/newslive">7*24</mt-tab-item>
        <mt-tab-item id="headlines" href="#/headlines">正点头条</mt-tab-item>
      </mt-navbar>
      <aside class="zdcj-h-aside" v-if="isSelected == true" @click="changeSelect">
        查看全部
      </aside>
      <aside class="zdcj-h-aside" v-else-if="isSelected == false" @click="changeSelect">
        只看精选
      </aside>
      <aside class="zdcj-h-aside" v-else></aside>
    </header>
    <div class="newsletter-container">
      <router-view :selectState="isSelected"></router-view>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      isSelected: false,
      selectedNav: 'newslive'
    }
  },

  methods: {
    changeSelect(){
      this.isSelected = !this.isSelected;
    }
  },

  watch: {
    '$route' (to, from) {
      if(to.path != '/newslive'){
        this.isSelected = null;
        this.selectedNav = 'headlines';
      } else {
        this.isSelected = false;
        this.selectedNav = 'newslive';
      }
    }
  },

  created(){

    if(this.$route.name == 'shixun'){
      this.$router.push({path: '/newslive'});
    }

    if(this.$route.path != '/newslive'){
      this.isSelected = null;
      this.selectedNav = 'headlines';
    } else {
      this.isSelected = false;
      this.selectedNav = 'newslive';
    }
  }
}
</script>

<style lang="scss">
  @import "./style/mixin";
  .zdcj-header {
    @include fixed-fill-box(50px);
    @include nav-style;
    .zdcj-h-nav {
      width: 70%;
      margin-left: 10%;
      background-color: transparent;
      .mint-tab-item {
        padding: 15px 0 12px;
        &.is-selected {
          color: #fff;
        }
        .mint-tab-item-label {
          font-size: 18px;
          line-height: 20px;
        }
      }
    }
    .zdcj-h-aside {
      position: absolute;
      top: 0;
      right: 0;
      width: 20%;
      font-size: 14px;
      line-height: 20px;
      padding: 15px 0;
      text-align: center;
    }
  }
  .newsletter-container {
    margin-top: 50px;
  }
</style>


