<template>
  <div class="bind-account animated fadeIn">
    <user-head pageTitle="绑定账户"></user-head>
    <mt-cell 
    v-for="(account, index) in accountArr"
    :key="index"
    :title="account.name">
      <mt-button type="primary" size="small" v-show="!account.isBind">立即绑定</mt-button>
      <img class="account-icon" slot="icon" :src="account.iconSrc">
    </mt-cell>
  </div>
</template>

<script>

import UserHead from './UserHead.vue'

import weiboIcon from '../assets/weibo.svg'
import qqIcon from '../assets/qq.svg'
import wechatIcon from '../assets/wechat.svg'
import emailIcon from '../assets/email.svg'

export default {
  data () {
    return {
      userInfo: [],
      accountArr: [
        {
          name: '新浪微博账号',
          iconSrc: weiboIcon,
          isBind: false
        },
        {
          name: 'QQ账号',
          iconSrc: qqIcon,
          isBind: false
        },
        // {
        //   name: '微信账号',
        //   iconSrc: wechatIcon,
        //   isBind: false
        // },
        {
          name: '邮箱账号',
          iconSrc: emailIcon,
          isBind: false
        }
      ]
    }
  },

  methods: {
    getUserInfo(){
      this.userInfo = this.getCookie();
      this.accountInfo = [this.userInfo[7], this.userInfo[6], this.userInfo[4]];
      this.accountArr.forEach((item, index) => {
        item.isBind = this.accountInfo[index] ? true : false;
      })
    }
  },

  created(){
    this.getUserInfo();
  },

  components: { UserHead }
}
</script>

<style lang="scss">
  .bind-account {
    .account-icon {
      width: 30px;
      height: 30px;
      margin-right: 10px;
    }
    .mint-cell {
      border-bottom: 1px solid #eaeaea;
    }
    .mint-cell-wrapper {
      padding: 20px;
    }
    .mint-button--small {
      border-radius: 4px;
    }
  }
  
</style>


