  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>

export default {
  data () {
    return {

    }
  },

  methods: {

  },

  created(){
    console.log(this.$route);
    if(this.$route.name == 'app'){
      this.$router.push({path: '/newslive'});
