import Vue from 'vue'
import VueRouter from 'vue-router'

import Index from './Index.vue'
import Headlines from './Headlines.vue'
import NewsLive from './NewsLive.vue'
import News from './News.vue'
import NewsDetail from './NewsDetail.vue'
import Category from './Category.vue'
import UserCenter from './UserCenter.vue'
import Markets from './Markets.vue'
import Calendar from './Calendar.vue'
import LoginPopup from './components/LoginPopup.vue'
import UserInfo from './components/UserInfo.vue'
import BindAccount from './components/BindAccount.vue'
import Favourite from './components/Favourite.vue'
import Comment from './components/Comment.vue'
import Test from './test.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'app', component: Index },
  
  { path: '/cate', name: 'fenlei', component: Category, 
  children: [
    { path: '/news', name: 'shixun', component: News, 
    children: [
      { path: '/headlines', name: 'zdtt', component: Headlines },
      { path: '/newslive', name: 'kuaixun', component: NewsLive },
    ]},
    { path: '/markets', name: 'hangqing', component: Markets },
    { path: '/calendar', name: 'rili', component: Calendar },
    { path: '/user', name: 'userPage', component: UserCenter },
  ]},
  { path: '/news/:name', name: 'newsDetail', component: NewsDetail },
  { path: '/login', name: 'loginPage', component: LoginPopup },
  { path: '/user/info', name: 'userinfo', component: UserInfo },
  { path: '/user/account', name: 'account', component: BindAccount },
  { path: '/user/favourite', name: 'favourite', component: Favourite },
  { path: '/user/comment', name: 'comment', component: Comment },
  { path: '/test', name: 'testPage', component: Test }
]

const Router = new VueRouter({
  routes // （缩写）相当于 routes: routes,
})

export default Router


