<template>
  <div class="comment-area animated fadeIn">
    <user-head pageTitle="我的评论"></user-head>
    <ul>
      <li class="comment-item" v-for="(item, index) in commentArr">
        <img class="user-avatar" :src="avatar" alt="">
        <div>
          <div class="comment-head">
            <p class="user-name">{{nickName}}</p>
            <p class="comment-time">{{item.time | dateFormat}}&nbsp;{{item.time | timeFormat}}</p>
          </div>
          <div class="comment-content">
            {{item.comment}}
          </div>
          <div class="compare-news">
            <span>来自原文：</span>
            <router-link
            :to="{name: 'newsDetail', params: {name: item.newsId}}">
              {{item.newsTitle}}
            </router-link>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

import UserHead from './UserHead.vue'

export default {
  data () {
    return {
      userInfo: [],
      nickName: '',
      avatar: '',
      commentArr: []
    }
  },

  methods: {
    getCommentInfo() {
      this.sendReq('I0032', {
        UserId: this.userInfo[0],
        TypeId: 1,
        PageIndex: 1,
        PageSize: 10
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          res.data.data.forEach((item, index) => {
            if(!item.title){
              return ;
            }
            item.Addtime = new Date(parseInt(item.Addtime.match(/\d+/)[0]));
            this.commentArr.push({
              newsId: item.ObjectId,
              newsTitle: item.title,
              comment: decodeURI(item.CommentInfo),
              time: item.Addtime
            })
          });
        }
      })
      .catch((err) => { console.log(err); });
    }
  },

  created(){
    this.userInfo = this.getCookie();
    this.nickName = this.userInfo[3];
    this.avatar = this.Api.avatarUrl + this.userInfo[2];
    this.getCommentInfo();
    console.log(this.filter);
  },

  components: { UserHead }
}
</script>

<style lang="scss">
  @import "../style/mixin";
  .comment-area {
    ul {
      list-style: none;
    }
  }
  .comment-item {
    position: relative;
    padding: 10px;
    padding-left: 70px;
    border-bottom: 1px solid #eaeaea;
    .user-avatar {
      position: absolute;
      top: 10px;
      left: 10px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden;
    }
    .comment-head {
      margin-bottom: 10px;
    }
    .user-name {
      line-height: 2;
      color: #0071ea;
    }
    .comment-time {
      color: #999;
      font-size: 14px;
      line-height: 18px;
    }
    .comment-content {
      margin-bottom: 10px;
      text-align: justify;
    }
    .mint-cell-wrapper {
      align-items: left;
      flex-direction: column;
      line-height: 2;
    }
    .mint-cell-title {
      margin-bottom: 10px;
    }
    .mint-cell-label {
      position: absolute;
      top: 4px;
      right: 10px;
      line-height: 32px;
    }

    .compare-news {
      position: relative;
      padding-left: 5.5em;
      padding-right: 5px;
      line-height: 2;
      background-color: #f2f2f2;
      a {
        height: 32px;
        color: #666;
        @include multiple-text-break($line: 1);
      }
      span {
        position: absolute;
        top: 0;
        left: 10px;
        color: #999;
      }
    }
  }
</style>


