<template>
  <div id="user-wrap" class="animated fadeIn">
    <div class="user-w-head">
      <div class="user-w-title">
        <div class="avatar">
          <img :src="avatar" alt="">
        </div>
        <div v-if="nickname" class="user-w-nickname">
          {{nickname}}
        </div>
        <div v-else class="user-login">
          <router-link to="/login">登录</router-link>
          <router-link to="/login">注册</router-link>
        </div>
      </div>
      <div class="user-hot-tab">
        <div class="tab-item l-tab">
          <a><i class="icon follow-icon"></i>关注</a>
        </div>
        <div class="tab-item r-tab">
          <router-link to="/user/comment"><i class="icon comment-icon"></i>评论</router-link>
        </div>
      </div>
    </div>
    <div class="user-nav">
      <mt-cell 
      class="tab-list"
      title="我的收藏"
      to="/user/favourite"
      is-link>
        <img slot="icon" src="./assets/tab-favorite.svg">
      </mt-cell>
      <mt-cell 
      class="tab-list"
      title="个人资料"
      to="/user/info"
      is-link>
        <img slot="icon" src="./assets/user-data.svg">
      </mt-cell>
      <mt-cell 
      class="tab-list"
      title="绑定账号"
      to="/user/account"
      is-link>
        <img slot="icon" src="./assets/bind-account.svg">
      </mt-cell>
      <mt-cell 
      class="tab-list"
      title="系统设置"
      is-link>
        <img slot="icon" src="./assets/setting.svg">
      </mt-cell>
      <mt-cell 
      class="tab-list"
      title="分享给好友"
      is-link>
        <img slot="icon" src="./assets/tab-share.svg">
      </mt-cell>
    </div>
  </div>
</template>

<script>

import defaultAvatar from './assets/avatar.svg';

export default {
  data () {
    return {
      avatar: defaultAvatar,
      nickname: ''
    }
  },

  methods: {

  },

  created(){
    if(this.checkLogin()){
      var userInfo = this.getCookie();
      console.log(userInfo);
      this.avatar = this.Api.avatarUrl + userInfo[2];
      this.nickname = userInfo[3];
    }
  }
}
</script>

<style lang="scss">
  @import './style/mixin';
  .user-w-head {
    height: 260px;
    background-image: url(./assets/userCenter-bg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    .user-w-title {
      box-sizing: border-box;
      width: 100px;
      height: 200px;
      margin: 0 auto;
      padding-top: 35px;
      .avatar {
        width: 100px;
        height: 100px;
        margin-bottom: 10px;
        border-radius: 50%;
        overflow: hidden;
        background-color: #fff;
      }
      .user-login {
        position: relative;
        display: flex;
        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 49%;
          width: 0;
          height: 20px;
          margin: 5px 0;
          border-right: 2px solid #fff;
        }
        a {
          flex: 1;
          text-align: center;
          line-height: 30px;
        }
      }
      .user-w-nickname {
        text-align: center;
        line-height: 30px;
        color: #fff;
      }
    }
    .user-hot-tab {
      height: 60px;
      display: flex;
      .tab-item {
        flex: 1;
        &.l-tab {
          padding-right: 1px;
        }
        &.r-tab {
          padding-left: 1px;
        }
        a {
          display: block;
          width: 100%;
          height: 100%;
          background-color: rgba(255, 255, 255, 0.3);
          text-align: center;
          line-height: 60px;
          .icon {
            display: inline-block;
            vertical-align: middle;
            margin-right: 5px;
            width: 20px;
            height: 20px;
            background-size: 100%;
            background-repeat: no-repeat;
          }
          .comment-icon {
            background-image: url(./assets/comment-icon.svg);
          }
          .follow-icon {
            background-image: url(./assets/follow-icon.svg);
          }
        }
      }
    }
  }
  .tab-list {
    padding: 5px 0;
    border-bottom: 1px solid #e8e8e8;
    .mint-cell-text {
      padding-left: 5px;
    }
    img {
      width: 24px;
      height: 24px;
    }
  }
</style>


