<template>
  <div class="user-info animated fadeIn">
    <user-head pageTitle="个人资料"></user-head>
    <div class="user-info-list">
      <mt-cell title="头像" is-link>
        <img class="avatar" :src="avatar" alt="">
        <UploadAvatar :userInfo="userInfo" @showPopup="showPopup" @setAvatar="setAvatar"></UploadAvatar>
      </mt-cell>
      <mt-cell class="input-group" title="昵称" is-link>
        <input type="text" v-model.lazy="nickName" @change="isChange = true">
        <div class="input-border"></div>
      </mt-cell>
      <mt-cell title="性别" :value="sex" is-link @click.native="showSex = true"></mt-cell>
      <mt-cell title="生日" :value="dateValue | dateFormat" is-link @click.native="showDateTime"></mt-cell>
      <mt-cell title="现居住地" :value="address" is-link @click.native="showAddr = true"></mt-cell>
      <mt-cell title="行业" :value="job" is-link @click.native="showJob = true"></mt-cell>
    </div>
    <div class="save-btn" v-show="isChange">
      <mt-button type="primary" size="large" @click="saveUserInfo">保存修改</mt-button>
    </div>
    <div class="logout-btn">
      <mt-button type="danger" size="large" @click="logout">退出</mt-button>
    </div>
    <mt-datetime-picker
      type="date"
      ref="dateTime"
      v-model="pickerValue"
      :startDate="new Date('1900-01-01')"
      :endDate="new Date()"
      @confirm="setDateTime">
    </mt-datetime-picker>
    <mt-popup
      class="popup-tips"
      v-model="tipVisible"
      position="top"
      :modal='false'>
      {{popUpText}}
    </mt-popup>
    <mt-popup
      class="sex-container"
      v-model="showSex"
      position="bottom">
      <mt-picker :slots="sexSlots" @change="onSexChange" :showToolbar="true">
          <span class="mint-datetime-action mint-datetime-cancel" @click="showSex = false">取消</span>
          <span class="mint-datetime-action mint-datetime-confirm" @click="setSex">确定</span>
      </mt-picker>
    </mt-popup>
    <mt-popup
      class="addr-container"
      v-model="showAddr"
      position="bottom">
      <mt-picker :slots="addressSlots" @change="onAddressChange" :showToolbar="true">
          <span class="mint-datetime-action mint-datetime-cancel" @click="showAddr = false">取消</span>
          <span class="mint-datetime-action mint-datetime-confirm" @click="setAddr">确定</span>
      </mt-picker>
    </mt-popup>
    <mt-popup
      class="addr-container"
      v-model="showJob"
      position="bottom">
      <mt-picker :slots="jobSlots" @change="onJobChange" :showToolbar="true">
          <span class="mint-datetime-action mint-datetime-cancel" @click="showJob = false">取消</span>
          <span class="mint-datetime-action mint-datetime-confirm" @click="setJob">确定</span>
      </mt-picker>
    </mt-popup>
  </div>
</template>

<script>

const addressArr = {
  '北京': ['北京'],
  '广东': ['广州', '深圳', '珠海', '汕头', '韶关', '佛山', '江门', '湛江', '茂名', '肇庆', '惠州', '梅州', '汕尾', '河源', '阳江', '清远', '东莞', '中山', '潮州', '揭阳', '云浮'],
  '上海': ['上海'],
  '天津': ['天津'],
  '重庆': ['重庆'],
  '辽宁': ['沈阳', '大连', '鞍山', '抚顺', '本溪', '丹东', '锦州', '营口', '阜新', '辽阳', '盘锦', '铁岭', '朝阳', '葫芦岛'],
  '江苏': ['南京', '苏州', '无锡', '常州', '镇江', '南通', '泰州', '扬州', '盐城', '连云港', '徐州', '淮安', '宿迁'],
  '湖北': ['武汉', '黄石', '十堰', '荆州', '宜昌', '襄樊', '鄂州', '荆门', '孝感', '黄冈', '咸宁', '随州', '恩施土家族苗族自治州', '仙桃', '天门', '潜江', '神农架林区'],
  '四川': ['成都', '自贡', '攀枝花', '泸州', '德阳', '绵阳', '广元', '遂宁', '内江', '乐山', '南充', '眉山', '宜宾', '广安', '达州', '雅安', '巴中', '资阳', '阿坝藏族羌族自治州', '甘孜藏族自治州', '凉山彝族自治州'],
  '陕西': ['西安', '铜川', '宝鸡', '咸阳', '渭南', '延安', '汉中', '榆林', '安康', '商洛'],
  '河北': ['石家庄', '唐山', '秦皇岛', '邯郸', '邢台', '保定', '张家口', '承德', '沧州', '廊坊', '衡水'],
  '山西': ['太原', '大同', '阳泉', '长治', '晋城', '朔州', '晋中', '运城', '忻州', '临汾', '吕梁'],
  '河南': ['郑州', '开封', '洛阳', '平顶山', '安阳', '鹤壁', '新乡', '焦作', '濮阳', '许昌', '漯河', '三门峡', '南阳', '商丘', '信阳', '周口', '驻马店'],
  '吉林': ['长春', '吉林', '四平', '辽源', '通化', '白山', '松原', '白城', '延边朝鲜族自治州'],
  '黑龙江': ['哈尔滨', '齐齐哈尔', '鹤岗', '双鸭山', '鸡西', '大庆', '伊春', '牡丹江', '佳木斯', '七台河', '黑河', '绥化', '大兴安岭地区'],
  '内蒙古': ['呼和浩特', '包头', '乌海', '赤峰', '通辽', '鄂尔多斯', '呼伦贝尔', '巴彦淖尔', '乌兰察布', '锡林郭勒盟', '兴安盟', '阿拉善盟'],
  '山东': ['济南', '青岛', '淄博', '枣庄', '东营', '烟台', '潍坊', '济宁', '泰安', '威海', '日照', '莱芜', '临沂', '德州', '聊城', '滨州', '菏泽'],
  '安徽': ['合肥', '芜湖', '蚌埠', '淮南', '马鞍山', '淮北', '铜陵', '安庆', '黄山', '滁州', '阜阳', '宿州', '巢湖', '六安', '亳州', '池州', '宣城'],
  '浙江': ['杭州', '宁波', '温州', '嘉兴', '湖州', '绍兴', '金华', '衢州', '舟山', '台州', '丽水'],
  '福建': ['福州', '厦门', '莆田', '三明', '泉州', '漳州', '南平', '龙岩', '宁德'],
  '湖南': ['长沙', '株洲', '湘潭', '衡阳', '邵阳', '岳阳', '常德', '张家界', '益阳', '郴州', '永州', '怀化', '娄底', '湘西土家族苗族自治州'],
  '广西': ['南宁', '柳州', '桂林', '梧州', '北海', '防城港', '钦州', '贵港', '玉林', '百色', '贺州', '河池', '来宾', '崇左'],
  '江西': ['南昌', '景德镇', '萍乡', '九江', '新余', '鹰潭', '赣州', '吉安', '宜春', '抚州', '上饶'],
  '贵州': ['贵阳', '六盘水', '遵义', '安顺', '铜仁地区', '毕节地区', '黔西南布依族苗族自治州', '黔东南苗族侗族自治州', '黔南布依族苗族自治州'],
  '云南': ['昆明', '曲靖', '玉溪', '保山', '昭通', '丽江', '普洱', '临沧', '德宏傣族景颇族自治州', '怒江傈僳族自治州', '迪庆藏族自治州', '大理白族自治州', '楚雄彝族自治州', '红河哈尼族彝族自治州', '文山壮族苗族自治州', '西双版纳傣族自治州'],
  '西藏': ['拉萨', '那曲地区', '昌都地区', '林芝地区', '山南地区', '日喀则地区', '阿里地区'],
  '海南': ['海口', '三亚', '五指山', '琼海', '儋州', '文昌', '万宁', '东方', '澄迈县', '定安县', '屯昌县', '临高县', '白沙黎族自治县', '昌江黎族自治县', '乐东黎族自治县', '陵水黎族自治县', '保亭黎族苗族自治县', '琼中黎族苗族自治县'],
  '甘肃': ['兰州', '嘉峪关', '金昌', '白银', '天水', '武威', '酒泉', '张掖', '庆阳', '平凉', '定西', '陇南', '临夏回族自治州', '甘南藏族自治州'],
  '宁夏': ['银川', '石嘴山', '吴忠', '固原', '中卫'],
  '青海': ['西宁', '海东地区', '海北藏族自治州', '海南藏族自治州', '黄南藏族自治州', '果洛藏族自治州', '玉树藏族自治州', '海西蒙古族藏族自治州'],
  '新疆': ['乌鲁木齐', '克拉玛依', '吐鲁番地区', '哈密地区', '和田地区', '阿克苏地区', '喀什地区', '克孜勒苏柯尔克孜自治州', '巴音郭楞蒙古自治州', '昌吉回族自治州', '博尔塔拉蒙古自治州', '石河子', '阿拉尔', '图木舒克', '五家渠', '伊犁哈萨克自治州'],
  '香港': ['香港'],
  '澳门': ['澳门'],
  '台湾': ['台北市', '高雄市', '台北县', '桃园县', '新竹县', '苗栗县', '台中县', '彰化县', '南投县', '云林县', '嘉义县', '台南县', '高雄县', '屏东县', '宜兰县', '花莲县', '台东县', '澎湖县', '基隆市', '新竹市', '台中市', '嘉义市', '台南市']
};
const sexArr = ['男', '女', '保密'];
const jobArr = ["金融", "教育", "IT", "媒体", "零售", "农业", "律师", "医疗服务", "其他"];

import defaultAvatar from '../assets/avatar.svg';
import UserHead from './UserHead.vue';
import UploadAvatar from './UploadAvatar.vue';
import Filter from '../utils/filters.js'

export default {
  data () {
    return {
      userInfo: [],
      nickName: '正点财经用户',
      avatar: defaultAvatar,
      address: '',
      pickerValue: new Date('1970-01-01'),
      dateValue: new Date('1970-01-01'),
      popUpText: '',
      tipVisible: false,
      showSex: false,
      sexSlots: [
        {
          defaultIndex: 3,
          values: sexArr
          // values: ['男', '女', '保密']
        }
      ],
      selectSex: '保密',
      sex: '保密',
      showAddr: false,
      addressSlots: [
        {
          flex: 1,
          values: Object.keys(addressArr),
          className: 'slot1',
          textAlign: 'center',
          defaultIndex: 3
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: ['北京'],
          className: 'slot3',
          textAlign: 'center'
        }
      ],
      addressProvince: '北京',
      addressCity: '北京',
      showJob: false,
      job: '',
      jobSlots: [
        {
          defaultIndex: 3,
          values: jobArr
        }
      ],
      selectJob: '其他',
      isChange: false
    }
  },

  methods: {
    showDateTime(){
      this.$refs.dateTime.open();
    },
    setDateTime(value){
      this.isChange = true;
      this.dateValue = value;
    },
    showPopup(text, callback){
      this.tipVisible = true;
      this.popUpText = text;
      setTimeout(() => {
        this.tipVisible = false;
        if(callback){
          callback();
        }
      }, 3000);
    },
    logout(){
      this.$cookie.set('zdcjcookie', '', { expires: 0 });
      this.showPopup('登出成功！3秒后跳转到首页。', function(){
        location.href = '#/';
      });
    },
    onSexChange(picker, values){
      if(values[0]){
        this.selectSex = values[0];
      }
    },
    onAddressChange(picker, values) {
      if(values[0]){
        picker.setSlotValues(1, addressArr[values[0]]);
        console.log(values);
        this.addressProvince = values[0];
        this.addressCity = values[1];
      }
    },
    onJobChange(picker, values) {
      if(values[0]){
        console.log(values);
        this.selectJob = values[0];
      }
    },
    setSex(){
      this.showSex = false;
      this.isChange = true;
      this.sex = this.selectSex;
    },
    setAddr(){
      this.showAddr = false;
      this.isChange = true;
      this.address = this.addressCity;
    },
    setJob(){
      this.showJob = false;
      this.isChange = true;
      this.job = this.selectJob;
    },
    setAvatar(){
      this.avatar = this.Api.avatarUrl + this.userInfo[2];
    },
    saveUserInfo(){
      this.sendReq('I0027', {
        UserId: this.userInfo[0],
        NickName: encodeURI(this.nickName),
        Sex: this.getSexIndex(),
        BirthDay: Filter.dateFormat(this.dateValue),
        Provice: encodeURI(this.addressProvince),
        City: encodeURI(this.addressCity),
        Industry: encodeURI(this.job)
      })
      .then((res) => {
        if(res.data.StatusCode == 0){
          this.showPopup(res.data.Info);
          this.userInfo[3] = this.nickName;
          this.userInfo[8] = this.addressProvince;
          this.userInfo[9] = this.addressCity;
          this.userInfo[10] = Filter.dateFormat(this.dateValue);
          this.userInfo[11] = this.getSexIndex();
          this.userInfo[12] = this.job;
          this.setCookie(this.userInfo);
          this.isChange = false;
        }
        console.log(res);
      })
      .catch((err) => { console.log(err); });
    },
    getSexIndex(){
      var nowIndex;
      sexArr.forEach((item, index) => {
        if(item == this.sex){
          nowIndex = index;
          return;
        }
      })
      return nowIndex || '2';
    }
  },

  created(){
    if(!this.checkLogin()){
      location.href = "#/login";
      return ;
    }
    this.userInfo = this.getCookie();
    console.log(this.userInfo);
    this.setAvatar();
    if(this.userInfo[3]){
      this.nickName = this.userInfo[3];
    }
    this.address = this.userInfo[9] || '火星';
    if(this.userInfo[10]){
      this.dateValue = this.pickerValue = new Date(this.userInfo[10]);
    }
    this.sex = sexArr[this.userInfo[11] || '2'];
    this.job = this.userInfo[12] || '其他';
  },

  mounted() {
    this.$nextTick(() => {
      this.sexSlots[0].defaultIndex = 0;
    });
  },

  components: {
    UserHead,
    UploadAvatar
  }
}
</script>

<style lang="scss">
  @import '../style/mixin';
  .user-info .mint-cell {
    border-bottom: 1px solid #eaeaea;
    .avatar {
      width: 50px;
      height: 50px;
      margin: 5px 0;
      border-radius: 50%;
    }
  }
  .user-info .avatar-btn {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
  .user-info-list {
    margin-bottom: 30px;
  }
  .logout-btn, .save-btn {
    padding: 0 10px;
    margin-bottom: 20px;
  }
  .img-crop-container {
    padding: 20px;
    .img-box {
      position: relative;
      width: 200px;
      height: 200px;
      margin-bottom: 10px;
      overflow: hidden;
      background-color: rgba(0, 0, 0, 0.15);
      img {
        position: absolute;
      }
    }
    .size-bar {
      margin-bottom: 10px;
    }
  }
  .popup-tips {
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
    background-color: rgba(0,0,0,.7);
  }
  .sex-container {
    width: 100%;
    .picker-slot {
      width: 100%;
    }
  }
  .addr-container {
    width: 100%;
  }
  .input-group {
    .mint-cell-mask {
      display: none;
    }
    .mint-cell-value {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 50;
      width: 100%;
      height: 100%;
      margin-right: 0;
    }
    input {
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      border: 0;
      outline: none;
      padding: 0;
      padding-left: 250px;
      padding-right: 34px;
      text-align: right;
      color: #888;
      font-size: 16px;
      background-color: transparent;
      &:focus {
        & + .input-border {
          transform: scale(1);
        }
      }
    }
    .input-border {
      position: absolute;
      bottom: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 0;
      border-top: 1px solid #26a2ff;
      transform: scale(0);
      transition: transform 0.3s;
    }
  }
</style>


